<template>
    <view class="copy">
        <view class="copy-value">
            {{value}}
        </view>
        <WPopup :show="show" @confirm="handleConfirm" @cancel="cancel">
            <template slot="title">
                请输入<i>已关联的公众号</i>的文章链接
            </template>
            <view class="copy-form">
                <u-textarea
                v-model="data" 
                type="textarea"
                :maxlength="-1"
                height="110"
                placeholder="仅支mp.weixin.qq.com开头的公众号文章链接"/>
            </view>
            <u-toast ref="uToast"></u-toast>
        </WPopup>
    </view>
</template>

<script>
    import WPopup from '../components/w_popup/index.vue'
    export default {
        components: {
            WPopup
        },
        props: {
            value: {
              type: [String, Number, Object, Array, Boolean],
              default: undefined,
            },
            option: {
                type: Object,
                default: {},
            },
        },
        data() {
            return {
				data: undefined,
                show: true,
                opened: false,
            }
        },
        watch: {
            data: {
                deep: true,
                handler(e) {
                    this.$emit('input', e)
                },
            },
            value: {
                deep: true,
                immediate: true,
                handler(e) {
                    this.data = e
                },
            },
        },
        methods: {
            cancel() {
                this.show = false
                if(!this.opened){
                    this.$emit('permit', false)
                }
            },
            handleConfirm() {
                if(this.value){
                    this.show = false
                    this.opened = true
                    this.$emit('permit', true)
                }else{
                    this.$refs.uToast.show({message: '请输入有效的公众号文章地址'})
                }
            },
            copy() {
                uni.setClipboardData({
                	data: this.value,
                	success: function () {
                		console.log('success');
                	}
                });
            },
        }
    }
</script>

